/** @jsx jsx */
import { jsx } from '@emotion/core';
import { Fragment, ReactNode, useState, useMemo } from 'react';
import SEO from 'components/Utils/SEO';
import { useLubyconWelcomeConsole } from 'hooks/useLubyconWelcomeConsole';
import { ThemeProvider, createMuiTheme } from '@material-ui/core/styles';
import CssBaseline from '@material-ui/core/CssBaseline';
import { GlobalContext } from 'src/stores';
import { User } from 'src/models/auth';
import { PaletteType, makeStyles } from '@material-ui/core';
import { getLocalStorageItem } from 'src/utils';
import { defaultTheme } from 'constants/theme';
import ThemeTypeToggleFab from 'components/Buttons/ThemeTypeToggleFab';

interface Props {
  children: ReactNode;
}

const App = ({ children }: Props) => {
  useLubyconWelcomeConsole();

  const [user, setUser] = useState<User>(null);
  const [themeType, setThemeType] = useState<PaletteType>('light');

  const theme = useMemo(() => {
    return createMuiTheme({
      ...defaultTheme,
      palette: {
        ...defaultTheme.palette,
        type: themeType,
      },
    });
  }, [themeType]);

  const style = makeStyles({
    fab: {
      position: 'fixed',
      bottom: 20,
      right: 20,
      zIndex: 2,
    },
  })();

  return (
    <GlobalContext.Provider value={{ user, themeType, setUser, setThemeType }}>
      <Fragment>
        <SEO />
        <ThemeProvider theme={theme}>
          <CssBaseline />
          {children}
          <ThemeTypeToggleFab className={style.fab} />
        </ThemeProvider>
      </Fragment>
    </GlobalContext.Provider>
  );
};

export default App;
